import React  from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';


const ListingDisplay = (props) => {

    const renderList = ({listdata}) => {
        if(listdata){
            return listdata.map((item) => {
                return(
                    <div className="row">
                        <Link to={`/details/${item._id}`}>
                            <div className="item-container col-md-10 col-xl-10 col-sm-10" key={item._id}>
                                <img className="rest-image col-md-4 col-sm-4 col-xs-4" src={item.thumb} alt=""/>
                                <div className="content col-md-6 col-sm-8 col-xs-8">
                                    <div className="hotel_name">
                                    {item.name}
                                    </div>
                                    <div className="city_name">
                                        {item.city_name}
                                    </div>
                                    <div className="city_name">
                                        {item.locality}
                                    </div>
                                    <div className="city_name">
                                        {item.address}
                                    </div>
                                    <div className="city_name">
                                        <i class="fa fa-glass" aria-hidden="true"></i>&nbsp;&nbsp;
                                        <i class="fa fa-cutlery" aria-hidden="true"></i>&nbsp;&nbsp;
                                        <i class="fa fa-plug" aria-hidden="true"></i>&nbsp;&nbsp;
                                        <i class="fa fa-wifi" aria-hidden="true"></i>&nbsp;
                                    </div>
                                    <h4 className="cuisine">
                                        {item.Cuisine[0].name} | {item.Cuisine[1].name}
                                    </h4>
                                    <div className="price-container">
                                        <div className="price">Rs. <strong>{item.cost}</strong></div>
                                    </div>
                                </div>                                
                            </div>
                        </Link>
                    </div>                    
                )
            })
        }else{
            return(
                <div>
                    <center>
                    <img src="/images/loading.gif" alt="gif"/>
                    </center>
                </div>
            )
        }
    }
    return(
        <div>
            <div className="row">
                
                <div className="col-md-12">
                    {renderList(props)}
                </div>
            </div>
        </div>
    )
}

export default ListingDisplay;